.custom-shape-divider-top-1656107443 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
}

.custom-shape-divider-top-1656107443 svg {
  position: relative;
  display: block;
  width: calc(116% + 1.3px);
  height: 210px;
}

.custom-shape-divider-top-1656107443 .shape-fill {
